.modal {
  position: fixed;
  top: 50%;
  left:50%;
  transform: translate(-50%, 10rem);
  width: 400px;
  height: auto;
  border: 1px solid $border-color;
  background-color: $sidebar-color;
  color: $font-color;
  box-shadow: .2rem 0 .5rem rgba(0,0,0,.2);
  // border-radius: 8px;
  z-index: 9999;
  //animation: fadeIn .2s cubic-bezier(.78,.32,.85,.8);

  &-toolbar {
    background-color: $background-color;
    cursor: move;
  }

  &-hide {
    animation-name: fadeOut;
    animation-duration: .2s;
    display:none;
  }

  &-show {
    display: block;
  }

  &-header {
    background-color: $background-color;
    padding: .5rem 1rem;
    border: 1px solid $border-color;
    border-bottom: none;
  }

  &-main {
    // position: relative;
    padding: 1rem;
    height: 100%;
    width: 100%;
    animation: formFadeIn .2s cubic-bezier(.17,.67,.83,.67);
  }

  &-footer {
    float: right;
    margin-bottom: 1rem;
  }
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0,0,0,.3);
  z-index: 9998;
  transition: background-color .25s;
}

@keyframes fadeIn {
  0% {transform: scale(0) translate(-50%,-50%) ;opacity: 0; transform-origin: top left;}
  20% {opacity: 0}
  50% {opacity: .5}
  90% {opacity: 1}
  100%{transform: scale(1) translate(-50%,-50%); opacity: 100%; transform-origin: top left;}
}

@keyframes fadeOut {
  0% {transform: scale(0); ; opacity: 1}
  100%{transform: scale(1); opacity: 1}
}

@keyframes formFadeIn {
  0% {opacity: 0}
  50% {opacity: .5}
  100%{opacity: 1}
}
