// Dark theme
$primary-color: #70BE43 !default; //#43a047
$primary-color-light: lighten($primary-color, 15%) !default;
$primary-color-dark: darken($primary-color, 15%) !default;

$secondary-color: #70BE43;//rgba(128,132,127,1);
// $success-color: color("green", "base") !default;
// $error-color: color("red", "base") !default;

$background-color: #303030;
$tile-background-color: #444; //lighten($background-color, 5%);
$header-background-color: rgba(0,0,0,.8);
$sidebar-color: #444;
$border-color: rgba(255,255,255,.20); //#555

@import url('https://fonts.googleapis.com/css?family=Raleway');
@font-face {
  font-family: 'Gotham';
  src: url('./font/Gotham-Book.otf');
}
$font-family: 'Gotham', 'Raleway';
$font-size: 10px; // 1 rem = 10px
$line-height: 1.5rem;
$font-color: rgba(255,255,255, .8);
$button-font-color: #333;
$header-color: rgba(255, 255, 255, .8); //font color
$active-tab-color: rgba(0,0,0,.8);

$link-color: lighten($primary-color,0%);
$body-link-color: $primary-color; //
$header-link-color: white;

