.tabs {
  box-shadow: 0 .5rem .5rem rgba(0,0,0,.25);
  background: $header-background-color;
  margin: 0;
  padding: 0;
  padding-top: .5rem;
  color: $header-color;
  font-size: .75rem;
}

.tab-item {
  display: inline-block;
  color: $font-color;
  padding: .5rem 1rem;
  min-width: 5rem;
  background: $background-color;
  // border-top-left-radius: 4px;
  // border-top-right-radius: 4px;
  // border: 1px solid $border-color;
  border-bottom: none;
  position: relative;
}

.tab-active {
  background: $secondary-color;//$primary-color;
  color: $active-tab-color;
  border: none;
}

.tab-delete {
  position: absolute;
  top: 0px;
  right: 1px;
}

.tab-edit {
  position: absolute;
  top: 1px;
  left: 1px;
}

.tab-icon {
  cursor: pointer;
}

.newtab-btn {
  margin: auto 1rem;
  margin-bottom:.25rem;
  vertical-align: middle;
  border-radius:100px;
  cursor: pointer;
  &:hover {
    background-color: #444;
    color: darken($font-color, 20%);
    transition: all .5s;
  }
}

.tab-modal {
  position: fixed;
  top: 45%;
  left:50%;
  width: 400px;
  height: auto;
  border: 1px solid $border-color;
  background-color: $sidebar-color;
  color: $font-color;
  box-shadow: .2rem 0 .5rem rgba(0,0,0,.2);
  border-radius: 8px;
  padding: 1rem;
  z-index: 9999;
  transform: translate(-50%,-50%);
  animation: TabFadeIn .2s cubic-bezier(.78,.32,.85,.8);

  &-header {
    margin: 0;
    margin-bottom: 1rem;
    font-size: 1.25rem;
  }
}

@keyframes TabFadeIn {
  0% {transform: scale(0) translate(-50%,-50%) ;opacity: 0; transform-origin: top left;}
  20% {opacity: 0}
  50% {opacity: .5}
  90% {opacity: 1}
  100%{transform: scale(1) translate(-50%,-50%); opacity: 1; transform-origin: top left;}
}
