.navbar {
	background-color: $header-background-color;
	min-height: 4vh;
	min-width:100%;
	height: 40px;
	display:flex;
	padding: .5rem;
	z-index: 0;
	position: relative;

	&-section {}

	&-asset {
		color: $header-color;
	}

  &-brand {
    color: $header-color;/*#558B2F;*/
    margin: auto;
    text-align: center;
		font-size: 1.5rem;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
  }

	&-menu-icon {
		color: $header-color;
		border-radius: 3px;
		&:hover {
			color: darken($font-color, 20%);
			transition: all .5s;
			&::after {
				content: '';
				height: 40px;
				width: 40px;
				border-radius: 20px;
			}
		}
	}

	&-navitem {
		display: inline-block;
		margin-right: 1rem;
		font-size: 1rem;
		&:hover {
			color: $primary-color;
			text-decoration: underline;
			transition: all .5s;
		}
		& a {
			color: white;
			cursor: pointer;
		}
	}
}
