.tile {
  background-color: $tile-background-color;
  font-size: 1rem;
  color: $font-color;
  position: relative;

  overflow: hidden;

  &-edit {
    border: 1px solid $border-color;
    box-shadow: 0 .1rem .15rem rgba(0,0,0,.5);
  }

  &-view {
    border: 1px solid transparent;
  }

  &-toolbar {
    background-color: rgba($background-color, .8);
    cursor: move;
  }

  &-remove {
    // position: absolute;
    // top: 5px;
    // right: 8px;
    cursor: pointer;
  }


}

.react-resizable-handle::after {
  border-right: 2px solid $font-color !important;
  border-bottom: 2px solid $font-color !important;
}

.no-resize span.react-resizable-handle{
  display: none;
}
